import React from 'react'
import {Link,Route} from 'react-router-dom'
import { connect } from 'react-redux';
import {Redirect} from 'react-router-dom';

const Cart = (props) => {

    let sc = props.login.shoucang;
    let location = props.location.pathname;
    let login = props.login.userinfo;
    console.log(sc);
    console.log(location);

    return (
        <div>
            {
                login == '' 
                ? 
                <Redirect to={{pathname:'/home/login',state:location}}/> 
                :
                <ul>
                    {
                        
                        sc.map((item,idx) => (
                            <li>
                                {item.title}
                                <button 
                                    onClick={() => {
                                        props.dispatch({
                                            type:'DELETE',
                                            index:idx
                                        })
                                    }}>删除</button>
                            </li>
                        ))
                    }
                </ul>
            }
        </div>
    )
}

// export default Cart
const mapStateToProps = (state) => {
    return {
        login: state.home
    }
}
export default connect(mapStateToProps)(Cart);