let databs = {
    datalist:[],
    shoucang:[],
    userinfo:''
}

const home = (state=databs,action) => {
    switch(action.type){
        case 'INSERT_IN':
            databs = Object.assign({},state,{
                datalist:[
                    ...state.datalist,
                    {
                        ...action.datalist
                    }
                ]
            });
            return databs;
        
        case 'CHANGE':
            let idx = action.index;
            console.log(idx);
            
            databs = Object.assign({},state,{
                shoucang:[
                    ...state.shoucang,
                    {
                        ...state.datalist[idx]
                    }
                ]
            })
            // console.log(databs);
            return databs;
        
        case 'DELETE':
            state.shoucang.splice(action.index,1);
            databs = Object.assign({},state,{
                shoucang:[
                    ...state.shoucang
                ]
            })
            return databs;

        case 'LOGIN':
            databs = Object.assign({},state,{
                userinfo:action.name
            })
            return databs;

        default:
            return databs;
    }
    
}

export default home;