import React,{useState} from 'react'
import {BrowserRouter as Router,Route,NavLink,Switch,Redirect,withRouter} from 'react-router-dom'
import Lunbo from './Lunbo'
import './Home.css'
import { ActivityIndicator } from 'antd-mobile';
import {connect, Provider} from 'react-redux'
import {login} from './actionCreators' 
import store from './store'


const Home = (props) => {
    console.log(props);
    window.onload = function(){
        props.dispatch(login());
    }
    // Math.random()
    return (

        <Provider store={store}>
            <div class='outside'>
                <div>
                    <NavLink to='/home/search'>
                        <div class='top'>
                            <div class='top-in'>
                                <i style={{fontSize:21}} className="iconfont icon-search"></i>
                                <span>寻找宝贝店铺</span>
                            </div>
                        </div>
                    </NavLink>
                </div>
                <Lunbo />

                <div class='bank'>
                    <img style={{width:161,height:19}} src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png'/>
                </div>



                <div class='detail'>
                    {
                        props.datalist != null,
                        props.datalist.map((item,idx) => (
                            
                            <NavLink to={{ pathname: '/home/commodity',name: 'switch',index:idx}}>
                                <div class='detaill'>
                                    <img src={item.img}/>
                                    <span class='intro'>{item.title}</span>
                                    <span class='price'>￥{item.price}</span>
                                    <span class='small'>95人已购买</span>
                                </div>
                            </NavLink>
                        ))
                    }


                    <div class='space'></div>
                    {/* <WhiteSpace size='xl'/> */}
                </div>
            </div>

        </Provider>
    )
}

const mapStateToProps = (state) => ({
    datalist:state.home.datalist
})

// export default Home
export default connect(mapStateToProps)(Home);



