import React from 'react'
import { Router } from 'react-router-dom'
import './Commodity.css'
import {connect, Provider} from 'react-redux'

const Commodity = (props) => {

    //获取数组值
    let data = props.datalist.home.datalist;
    //判断登录
    let login = props.datalist.home.userinfo;
    let location = props.location.pathname;
    //根据id来存储，判断收藏里是否有该id
    let idx = props.location.index;
    let id = data[idx].id;
    let sc = props.datalist.home.shoucang;
    

    let name = 'iconfont icon-shoucang';
    for (let i = 0; i < sc.length; i++) {
        if (sc[i].id == id) {
            name = 'iconfont icon-shoucang1';
            break;
        }
        name = 'iconfont icon-shoucang';
    }
    const changename = (f) => {
        if(login == ''){
            props.history.push({pathname:'/home/login',state:location,index:idx})
        }
        let name = f.target.className;
        if(name == 'iconfont icon-shoucang'){
            props.dispatch({
                type:'CHANGE',
                index:idx
            })
        }else{
            props.dispatch({
                type:'DELETE',
                index:idx
            })
        }
    }

    return (
        <div>
            <div className='com-top'>
                <div className='back' onClick={() => (props.history.push('/'))}>
                    <i style={{fontSize:21}} className='iconfont icon-fanhui'></i>
                </div>
                <img className='shangpin' src={data[idx].img}/>
            </div>


            <div className='com-bot'>
                <div className='shop'>
                    <i style={{fontSize:25}} className='iconfont icon-dianpu'></i>
                    <span>店铺</span>
                </div>
                <div className='wangwang'>
                    <i style={{fontSize:25}} className='iconfont icon-aliwangwang'></i>
                    <span>客服</span>
                </div>
                <div className='fav'>
                    <i style={{fontSize:25}} className={name} onClick={(f) => {changename(f)}}></i>
                    <span>收藏</span>
                </div>


                <div className='bot-left'>
                    <span>加入购物车</span>
                </div>
                <div className='bot-right'>
                    <span>立即购买</span>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    datalist:state
})

export default connect(mapStateToProps)(Commodity);
