import React from 'react'
import {connect, Provider} from 'react-redux'

const Login = (props) => {

    const login = () => {
        props.dispatch({
            type:'LOGIN',
            name:'lijinjie'
        })
        props.history.push({pathname:props.location.state,index:props.location.index})
    }
    return (
        <div>
            <button onClick={login}>登录</button>
            {
                console.log(props)
            }
        </div>
    )
}

const mapStateToProps = (state) => {
        return {
        datalist: state
    }
}

export default connect(mapStateToProps) (Login);